.center{
    display: grid;
    grid-template-areas:
            'mh'
            'm'
            'sm'
;
    grid-template-rows: 3fr 400px 3fr;
    border: #e2e2e2 1px solid;
    border-left: 0;

}

.messages{
    grid-area: m;
    background: #fafafa;
    border-bottom: #e2e2e2 1px solid;
    display: flex;
    flex-direction: column;
    overflow: scroll;

}

.message_header{
    grid-area: mh;
    background: #f5f5f5;
    border-bottom: #e2e2e2 1px solid;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.user_name{
    font-size: 30px;
    margin-left: 10px;
}

.send_message{
    grid-area: sm;
    background: #f5f5f5;
}

.left_m{
    align-self: center;
    width: content-box;
    background: #3c4151;
    color: white;
    border-radius: 20px;
    padding: 5px 20px;
    margin-left: 5px;

}

.right_m{
    width: content-box;
    background: #e0e0e0;
    color: black;
    border-radius: 20px;
    padding: 5px 20px;
    margin-top: 20px;
    margin-right: 10px;
}

.img{
    width: 50px;
    height: 50px;
    border-radius: 100%;
    margin-left: 10px;
}

.container{
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    margin-left: 10px;
}

.container_r{
   align-self: flex-end;
}

.date{
    color: #3c4151;
    font-size: 10px;
    margin: 10px ;
}

.input{
    width: 80%;
    border-radius: 20px;
    height: 30px;
    border: #e2e2e2 1px solid;
    margin: 20px 0 20px 20px;
    padding-left: 10px;
}

.btn{
    border: 0;
    background: transparent;
    font-size: 20px;
}
