.left {
    display: grid;
    grid-template-areas: 'h' 'c';
    grid-template-rows: 150px 400px;
    border: #e2e2e2 1px solid;
}


.header {
    grid-area: h;
    background: #f5f5f5;
    border-bottom: #e2e2e2 1px solid;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}


.img {
    width: 70px;
}

.search {
    width: 60%;
    border-radius: 20px;
    height: 30px;
    border: #e2e2e2 1px solid;
    align-self: center;
    margin: 5px ;
    padding-left: 10px;
}

.btn{
    border: 0;
    background: transparent;
    font-size: 20px;
}

.chats {
    grid-area: c;
    background: #ffffff;
    overflow: auto;
}

.chat_header{
    color: #6facc3;
    font-size: 30px;
    margin-left: 10px;
}

.result{
    display: flex;
    flex-direction: column;
    background: #76d4b4;
    padding: 10px;
    border-bottom:  #e2e2e2 1px solid;
}

.subresult{
    display: flex;
    flex-direction: row;
}

.timestamp{
    color: #3c4151;
    font-size: 15px;
}
